___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.app {
    margin: 40px auto;
    width: 80vw;
    outline: 1px solid black;
    font-family: Roboto;
    font-size: 18px;
    min-width: 300px;
    max-width: 800px;
}

.app__header,
.app__footer {
    display: flex;
    justify-content: space-between;
    padding: 5px;
}

.app__game-field {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-template-rows: repeat(5, auto);
}

.cell {
    width: 30px;
    height: 30px;
    outline: 1px solid black;
    background: #ffffff;
}

.cell:active {
    background-color: #ffffff;
}

.button {
    cursor: pointer;
    background: #ffffff;
    height: 60px;
    width: 100px;
    border-radius: 6px;
    font-family: Roboto;
    transition: all 0.5s;
    font-size: 18px;
}

#vertical-clues,
#horizontal-clues {
    display: flex;
    outline: 1px solid black;
}

#horizontal-clues {
    flex-direction: column;
}

#inner-wrapper {
    display: flex;
}

#game-field-wapper {
    display: inline-flex;
    flex-direction: column;
    width: min-content;
    margin: 0 50%;
    transform: translateX(-50%);
    padding: 20px 0;
}

@media (hover: hover) {
    .button:hover {
        background: rgb(194, 194, 194);
    }
}
